export default {
    '@global': {
        '.kuc-alert': {
            position: 'relative',
            display: 'block',
            margin: '8px 0',
            padding: '4px 18px',
            background: '#e74c3c',
            color: '#f6f6f6',
            '&.bg-danger': {
                background: '#e74c3c',
            },
            '&.bg-success': {
                background: '#91C36C',
            }
        }
    }
};
