export default {
    '@global': {
        '.kuc-attachment': {
            '&-outer': {
                width: '293px',
                height: 'auto',
                'box-sizing': 'border-box',
                position: 'relative'
            },
            '&-file': {
                border: 'solid 1px #e3e7e8',
                'background-color': '#eee',
                padding: '16px 8px',
                display: 'block',
                'font-size': '14px',
                overflow: 'hidden',
                position: 'relative',
                '&-droppable': {
                    display: 'block',
                    position: 'absolute',
                    top: '0',
                    left: '0',
                    margin: 'auto 0',
                    '&-text': {
                        'background-color': '#e2f2fe',
                        border: 'dashed 2px #3498db',
                        display: 'table-cell',
                        'vertical-align': 'middle',
                        'text-align': 'center',
                        color: '#3498db',
                        'font-size': '14px',
                    }
                },
                '&-upload': {
                    '&-button': {
                        border: '1px solid transparent',
                        position: 'relative',
                        display: 'inline-block',
                        'margin-right': '16px',
                        padding: '8px',
                        'text-decoration': 'none',
                        '&:focus-within': {
                            border: '1px solid #3498db',
                        },
                        '&-text': {
                            color: '#3498db',
                            'font-size': '14px',
                        },
                        '&:hover &-text': {
                            border: '1px solid #3498db',
                        },
                    },
                    '&-html5': {
                        opacity: '0',
                        position: 'absolute',
                        top: '0',
                        left: '0',
                        right: '0',
                        bottom: '0',
                        overflow: 'hidden',
                        display: 'inline-block',
                        '& input': {
                            cursor: 'pointer',
                            'font-size': '999px',
                            'vertical-align': 'middle',
                            height: '100%',
                            width: '100%',
                            'line-height': '1.5',
                        }
                    }
                },
                '&-constraints': {
                    display: 'inline-block',
                    color: '#888',
                    margin: '3px 0 0',
                    'font-size': '14px',
                },
                '&-item': {
                    position: 'relative',
                    'margin-bottom': '8px',
                    height: '24px',
                    border: '2px solid #f1f4f5',
                    'background-color': '#f1f4f5',
                },
                '&-error': {
                    background: '#e74c3c',
                    position: 'relative',
                    display: 'block',
                    margin: '8px 0',
                    padding: '4px 18px',
                    color: '#f6f6f6',
                    'font-size': '14px',
                }
            },
            '&-file-item &_file_name': {
                display: 'inline-block',
                padding: '3px 68px 0 26px',
                width: '100%',
                'box-sizing': 'border-box',
                'white-space': 'nowrap',
                overflow: 'hidden',
                'text-overflow': 'ellipsis',
                'word-wrap': 'normal',
                'font-size': '14px',
            },
            '&-file-item &_file_action': {
                display: 'inline-block',
                position: 'absolute',
                top: '0',
                left: '0',
                width: '24px',
                height: '24px',
                '& button': {
                    display: 'inline-block',
                    width: '24px',
                    height: '24px',
                    'margin-right': '4px',
                    background: '#f1f4f5 url(\'data:image/png\',base64,\'\') no-repeat 5px center',
                    border: '1px solid transparent',
                    'font-size': '0',
                    'line-height': '1.5',
                    '-webkit-appearance': 'button',
                    cursor: 'pointer',
                    'text-transform': 'none',
                }
            },
            '&-file-item &_file_size': {
                display: 'inline-block',
                position: 'absolute',
                top: '0',
                right: '0',
                color: '#888',
                padding: '3px 3px 0 0',
                'max-width': '64px',
                'white-space': 'nowrap',
                overflow: 'hidden',
                'text-overflow': 'ellipsis',
                'word-wrap': 'normal',
                'font-size': '14px',
            },
            '&-drag-drop-active': {
                overflow: 'hidden',
                border: 'none',
            },
            '&-drag-drop-active &-file-filelist, &-drag-drop-active &-upload-button, &-drag-drop-active &-constraints': {
                visibility: 'hidden',
            }
        }
    }
};
