export default {
    '@global': {
        '.kuc-btn': {
            '&.normal, &.submit': {
                '-webkit-user-select': 'none',
                '-moz-user-select': 'none',
                '-ms-user-select': 'none',
                'user-select': 'none',
                'font-size': '16px',
                'white-space': 'nowrap',
                '&:disabled, &:disabled:hover': {
                    'color': '#bababa',
                    'border': '1px solid #e3e7e8',
                    'background-color': '#dbdcdd',
                    'cursor': 'not-allowed',
                    'box-shadow': 'none'
                }
            },
            '&.normal': {
                'display': 'block',
                'box-sizing': 'border-box',
                'padding': '0 16px',
                'min-width': '163px',
                'height': '48px',
                'outline': 'none',
                'border': '1px solid #e3e7e8',
                'background-color': '#f7f9fa',
                'box-shadow': '1px 1px 1px #fff inset',
                'color': '#3498db',
                'text-align': 'center',
                'line-height': '48px',
                '&:hover': {
                    'background-color': '#c8d6dd',
                    'box-shadow': 'none',
                    'cursor': 'pointer',
                }
            },
            '&.submit': {
                'display': 'block',
                'box-sizing': 'border-box',
                'padding': '0 16px',
                'min-width': '163px',
                'height': '48px',
                'outline': 'none',
                'border': '1px solid #e3e7e8',
                'background-color': '#3498db',
                'box-shadow': '1px 1px 1px #8ccbee inset',
                'color': '#fff',
                'text-align': 'center',
                'line-height': '48px',
                '&:hover': {
                    'background-color': '#1d6fa5',
                    'cursor': 'pointer',
                }
            }
        },
    }
};
